body {
  background: #F8F9FA;
  font-family: PingFangSC-Regular;
}
ol {
  list-style: none;
  -webkit-padding-start: 0;
  margin-bottom: 0;
}
.ant-layout-content {
  margin: 0;
}
.body {
  width: 800px;
  margin: 15px auto;
}
.body .aside {
  float: left;
  width: 160px;
  margin-top: 100px;
}
.body .aside .box {
  float: left;
  width: 158px;
  background: #F8F9FA;
  border: 1px solid #DEE2E6;
}
.body .aside .box .title {
  display: block;
  font-size: 14px;
  color: #495057;
  letter-spacing: 0;
  line-height: 20px;
  margin: 16px 0 16px 24px;
}
.body .aside .box ul {
  float: left;
  list-style: none;
  -webkit-padding-start: 0;
  margin-left: 24px;
}
.body .aside .box ul li {
  float: left;
  width: 112px;
  height: 80px;
  background: #FFFFFF;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border-radius: 3px;
  cursor: pointer;
  text-align: center;
  margin-bottom: 16px;
  position: relative;
  cursor: move;
}
.body .aside .box ul li i {
  font-size: 22px;
  margin-top: 22px;
  margin-bottom: 9px;
  color: #868E96;
}
.body .aside .box ul li p {
  font-size: 14px;
  line-height: 20px;
  color: #868E96;
}
.body .aside .box ul li .item_s_l {
  position: absolute;
  left: 0;
  left: 30px;
  top: 23px;
}
.body .aside .box ul li .item_s_l i {
  font-size: 4px;
  margin-top: 0;
  color: #CED4DA ;
}
.body .aside .box ul li .item_s_r {
  position: absolute;
  right: 30px;
  top: 23px;
}
.body .aside .box ul li .item_s_r i {
  font-size: 4px;
  margin-top: 0;
  color: #CED4DA ;
}
.body .aside .message {
  float: left;
  text-align: center;
  font-size: 12px;
  color: #F76707;
  letter-spacing: 0;
  line-height: 16px;
  padding: 27px 20px;
}
.body .bodycontet {
  float: left;
  width: 600px;
  overflow: hidden;
  margin-left: 40px;
  margin-top: 100px;
}
.body .bodycontet ol li {
  float: left;
  width: 100%;
  margin-top: 16px;
}
.body .bodycontet ol li img {
  cursor: move;
}
.body .bodycontet ol li .line {
  float: left;
  width: 100%;
  height: 152px;
  background: #F8F9FA;
  border: 1px dashed #DEE2E6;
  line-height: 152px;
  text-align: center;
}
.body .bodycontet ol li .line span {
  font-size: 11px;
  color: #B5B5B5;
}
.body .bodycontet .item {
  float: left;
  width: 100%;
  height: 100px;
  background: #FFFFFF;
  border-radius: 3px;
  margin-top: 16px;
}
.body .bodycontet .Tablist {
  float: left;
  width: 598px;
  background: #FFFFFF;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  height: 49px;
  margin-top: 16px;
}
.body .bodycontet .Tablist ul {
  list-style: none;
  -webkit-padding-start: 0;
  text-align: center;
}
.body .bodycontet .Tablist ul li {
  display: inline-block;
  font-size: 15px;
  color: #000000;
  margin: 0 30px;
  line-height: 45px;
  border-bottom: 3px solid white;
  letter-spacing: 0;
  cursor: pointer;
}
.body .bodycontet .Tablist ul li.active {
  border-bottom: 3px solid  #AE7D4B;
  color: #AE7D4B;
}
.body .bodycontet .nodate {
  line-height: 100px;
  text-align: center;
  height: 100px;
}
.body .bodycontet .loaddate {
  float: left;
  width: 600px;
  height: 50px;
  text-align: center;
  background: #FFFFFF;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  color: #AE7D4B;
  font-size: 15px;
  line-height: 50px;
  margin-top: 16px;
  cursor: pointer;
}



