html,
body,
:global(#root) {
  height: 100%;
}
:global(.ant-layout) {
  min-height: 100%;
}
canvas {
  display: block;
}
body {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.globalSpin {
  width: 100%;
  margin: 40px 0 !important;
}
:global .ant-spin-container {
  overflow: visible !important;
}



