html,
body {
  position: relative;
  height: 100%;
  width: 100%;
  min-width: 1280px;
}
input,
ul,
li,
p,
img,
h1,
h2,
h3,
table,
tr,
td {
  margin: 0;
  padding: 0;
}
img {
  width: 100%;
  height: 100%;
  border-radius: 3px;
}
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}
a,
input,
textarea,
select {
  outline: 0;
}
p {
  margin: 0;
  overflow: hidden;
  padding: 0;
}
i {
  font-style: normal;
}
ul,
li,
dl,
dt,
dd {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
:global .h2 {
  font-size: 16px;
  color: #495057;
  line-height: 24px;
  font-weight: bold;
}
:global .alink {
  color: #40C057;
}
:global .alink:active,
:global .alink:hover {
  color: rgba(83, 198, 103, 0.9);
}
:global .ant-btn {
  border-radius: 3px;
}
:global .ant-btn-primary:hover,
:global .ant-btn-primary {
  background-image: linear-gradient(-45deg, #7048E8 0%, #7F46E2 18%, #8D44DC 37%, #9942D5 55%, #A440CF 74%, #AE3EC9 92%);
}
:global .ant-tabs {
  color: #868E96!important;
}
:global .ant-tabs-nav .ant-tabs-tab {
  padding-bottom: 22px!important;
}
:global .ant-tabs-ink-bar {
  height: 1px!important;
}
:global .ant-tabs-bar {
  margin-bottom: 22px!important;
}
:global .ant-form-item .ant-form-item-label {
  text-align: left;
}
:global .ant-form-inline .ant-form-item {
  margin-right: 24px!important;
}
:global .fl {
  float: left;
}
:global .fr {
  float: right;
}
:global .picList .anticon-delete {
  display: none;
}
:global .table {
  border: 1px solid #dee2e6;
  border-bottom: none;
  margin: 16px 0 36px 0;
}
:global .ant-upload-list-picture-card .ant-upload-list-item {
  padding: 0!important;
  margin: 0 16px 16px 0!important;
  width: 64px!important;
  height: 64px!important;
}
:global .imagePreview .ant-modal-close-x {
  width: 28px;
  height: 28px;
  line-height: 28px;
}
:global .ant-upload.ant-upload-select-picture-card {
  margin: 0 16px 16px 0!important;
  width: 64px!important;
  height: 64px!important;
}



