.Follow {
  float: left;
  width: 598px;
  height: 86px;
  overflow: hidden;
  background: white;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.Follow dl {
  float: left;
  width: 598px;
  overflow: hidden;
}
.Follow dl dt {
  float: left;
  width: 56px;
  margin: 15px 0 0 20px;
}
.Follow dl dt img {
  width: 56px;
  border-radius: 3px;
  max-width: 100%;
}
.Follow dl dd {
  float: left;
  width: 490px;
  margin-left: 15px;
}
.Follow dl dd .title {
  float: left;
  width: 490px;
  font-size: 18px;
  color: #000000;
  letter-spacing: 0;
  line-height: 18px;
  margin-top: 16px;
  overflow: hidden;
}
.Follow dl dd .title span.sim_title {
  float: left;
  max-width: 440px;
  height: 18px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.Follow dl dd .title span {
  float: left;
}
.Follow dl dd .title span img {
  width: 35px;
  margin-left: 5px;
}
.Follow dl dd .rfcontaion {
  float: left;
  font-size: 10px;
  margin-top: 3px;
}
.Follow dl dd .rfcontaion .srate {
  font-size: 10px;
}
.Follow dl dd .rfcontaion .fans {
  font-size: 9px;
  color: #757575;
  letter-spacing: 0;
  line-height: 18px;
}
.Follow dl dd .followbtn {
  float: right;
}
.Follow dl dd .followbtn span {
  float: left;
  width: 60px;
  height: 26px;
  background: #AE7D4B;
  border-radius: 2px;
  font-size: 11px;
  color: #FFFFFF;
  text-align: center;
  line-height: 26px;
}



