.Singcard {
  float: left;
  width: 598px;
  height: auto;
  border: 1px solid #DEE2E6;
}
.Singcard .header {
  float: left;
  width: 596px;
  line-height: 32px;
  font-size: 12px;
  color: #868E96;
  border-bottom: 1px solid #DEE2E6;
  background: white;
}
.Singcard .header .e_btn {
  float: left;
  padding: 0 10px;
  border-left: 1px solid #DEE2E6;
  cursor: pointer;
}
.Singcard .header .e_btn i {
  margin: 0 5px;
  font-size: 14px;
}
.Singcard .header .e_btn:fast-child {
  border-left: none;
}
.Singcard .selction {
  float: left;
  width: 598px;
}
.Singcard .selction img {
  max-width: 100%;
  width: 598px;
  border-radius: 0px;
}



