.Singcard {
  float: left;
  width: 598px;
  height: auto;
  border: 1px solid #DEE2E6;
  margin-top: 16px;
}
.Singcard .header {
  float: left;
  width: 596px;
  line-height: 32px;
  font-size: 12px;
  color: #868E96;
  border-bottom: 1px solid #DEE2E6;
  background: white;
}
.Singcard .header .e_btn {
  float: left;
  padding: 0 10px;
  border-left: 1px solid #DEE2E6;
  cursor: pointer;
}
.Singcard .header .e_btn i {
  margin: 0 5px;
  font-size: 14px;
}
.Singcard .header .e_btn:fast-child {
  border-left: none;
}
.Singcard .selction {
  float: left;
  width: 598px;
}
.Singcard .selction img {
  max-width: 100%;
}
.Singcard .hotlist {
  float: left;
  background: #FFFFFF;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  margin: 8px;
}
.Singcard .hotlist .hotlist_title {
  float: left;
  height: 54px;
  line-height: 54px;
  width: 580px;
  font-size: 17px;
  color: #000000;
  text-indent: 25px;
  border-bottom: 1px solid #DEE2E6;
}
.Singcard .hotlist .hotlist_list {
  float: left;
  width: 580px;
  overflow: hidden;
}
.Singcard .hotlist .hotlist_list dl {
  float: left;
  width: 290px;
  border-right: 1px solid #DEE2E6;
  padding-bottom: 15px;
  border-bottom: 1px solid #DEE2E6;
  margin-bottom: 0px;
}
.Singcard .hotlist .hotlist_list dl dt {
  float: left;
  width: 167px;
  height: 167px;
  overflow: hidden;
  margin-left: 60px;
  margin-top: 10px;
  text-align: center;
}
.Singcard .hotlist .hotlist_list dl dt img {
  max-width: 100%;
}
.Singcard .hotlist .hotlist_list dl dd {
  float: left;
  width: 290px;
  margin-top: 28px;
}
.Singcard .hotlist .hotlist_list dl dd p {
  font-size: 13px;
  color: #000000;
  text-indent: 24px;
  margin-bottom: 0px;
  width: 290px;
  height: 24px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.Singcard .hotlist .hotlist_list dl dd span.price {
  float: left;
  font-size: 19px;
  color: #BF2634;
  margin-left: 24px;
}
.Singcard .hotlist .hotlist_list dl dd span.free {
  float: left;
  margin-left: 7px;
}
.Singcard .hotlist .hotlist_list dl dd span.free img {
  float: left;
  width: 44px;
}



